@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
}

img {
  max-width: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media (min-width: 1140px) {
  .container {
    max-width: 1140px;
    padding-left: 0;
    padding-right: 0;
  }
}

header {
  background-color: #222222;
}

header .header-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px;
}

header .header-block__logo {
  display: flex;
  align-items: center;
}

header .header-block__logo img {
  margin-right: 40px;
}

header .header-block__menu {
  display: flex;
  align-items: center;
}

header .header-block__menu a {
  margin-left: 33px;
}

header .header-block__cabinet {
  display: none;
}

@media (min-width: 768px) {
  header .header-block__cabinet {
    display: block;
  }
}

@media (min-width: 1140px) {
  header .header-block__cabinet {
    display: block;
  }
}

header .header-block__cart {
  position: relative;
  display: none;
}

@media (min-width: 768px) {
  header .header-block__cart {
    display: block;
  }
}

@media (min-width: 1140px) {
  header .header-block__cart {
    display: block;
  }
}

header .header-block__cart-count {
  position: absolute;
  top: -10px;
  right: -10px;
  color: #fff;
  width: 19px;
  height: 19px;
  border-radius: 50%;
  background-color: #F16D7F;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
}

.header-banner {
  background-color: #F1E4E6;
}

.header-banner .header-banner__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: none;
  background-position: -220px center;
  background-repeat: no-repeat;
  height: 363px;
}

@media (min-width: 768px) {
  .header-banner .header-banner__wrapper {
    height: 367px;
    background-image: url("../img/slide-1.png");
    background-size: contain;
    background-position: left center;
    justify-content: end;
  }
}

@media (min-width: 1140px) {
  .header-banner .header-banner__wrapper {
    background-image: url("../img/slide-1.png");
    background-position: -220px center;
    height: 764px;
    background-size: contain;
  }
}

.header-banner .header-banner__title {
  position: relative;
  font-weight: 900;
  font-size: 38px;
  line-height: 38px;
  color: #222222;
  width: auto;
  padding-left: 28px;
  padding-top: 10px;
}

@media (min-width: 768px) {
  .header-banner .header-banner__title {
    font-size: 44px;
    width: 45%;
  }
}

@media (min-width: 1140px) {
  .header-banner .header-banner__title {
    font-size: 48px;
  }
}

.header-banner .header-banner__title:before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 12px;
  height: 92px;
  background-color: #F16D7F;
}

.header-banner .header-banner__subtitle {
  font-weight: 700;
  font-size: 20px;
  line-height: normal;
}

@media (min-width: 768px) {
  .header-banner .header-banner__subtitle {
    font-size: 24px;
  }
}

@media (min-width: 1140px) {
  .header-banner .header-banner__subtitle {
    font-size: 32px;
  }
}

.header-banner .red {
  color: #F16D7F;
}

.main-block {
  margin-top: 64px;
}

@media (min-width: 768px) {
  .main-block {
    margin-top: 20px;
  }
}

@media (min-width: 1140px) {
  .main-block {
    margin-top: 65px;
  }
}

.main-block .card-1, .main-block .card-2, .main-block .card-3, .main-block .card-4 {
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-position: top;
}

@media (min-width: 768px) {
  .main-block .card-1, .main-block .card-2, .main-block .card-3, .main-block .card-4 {
    min-height: 167px;
    background-position: center;
  }
}

@media (min-width: 1140px) {
  .main-block .card-1, .main-block .card-2, .main-block .card-3, .main-block .card-4 {
    min-height: 260px;
    background-position: center;
  }
}

.main-block .card-1 {
  background-image: linear-gradient(rgba(33, 22, 22, 0.7), rgba(33, 22, 22, 0.7)), url("../img/card-1.jpg");
  grid-area: card-1;
  min-height: 247px;
}

@media (min-width: 768px) {
  .main-block .card-1 {
    min-height: 167px;
  }
}

@media (min-width: 1140px) {
  .main-block .card-1 {
    min-height: 260px;
  }
}

.main-block .card-2 {
  background-image: linear-gradient(rgba(33, 22, 22, 0.7), rgba(33, 22, 22, 0.7)), url("../img/card-2.jpg");
  grid-area: card-2;
  min-height: 247px;
}

@media (min-width: 768px) {
  .main-block .card-2 {
    min-height: 167px;
  }
}

@media (min-width: 1140px) {
  .main-block .card-2 {
    min-height: 260px;
  }
}

.main-block .card-3 {
  background-image: linear-gradient(rgba(33, 22, 22, 0.7), rgba(33, 22, 22, 0.7)), url("../img/card-3.jpg");
  grid-area: card-3;
  min-height: 247px;
}

@media (min-width: 768px) {
  .main-block .card-3 {
    min-height: 167px;
  }
}

@media (min-width: 1140px) {
  .main-block .card-3 {
    min-height: 260px;
  }
}

.main-block .card-4 {
  background-image: linear-gradient(rgba(33, 22, 22, 0.7), rgba(33, 22, 22, 0.7)), url("../img/card-4.jpg");
  min-height: 111px;
  grid-area: card-4;
}

@media (min-width: 768px) {
  .main-block .card-4 {
    min-height: 116px;
  }
}

@media (min-width: 1140px) {
  .main-block .card-4 {
    min-height: 180px;
  }
}

.main-block .cards {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, minmax(1fr, 1fr));
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "card-1" "card-2" "card-3" "card-4";
}

@media (min-width: 768px) {
  .main-block .cards {
    grid-template-columns: repeat(auto-fit, minmax(232px, 232px));
    gap: 20px;
    grid-template-areas: "card-1 card-2 card-3" "card-4 card-4 card-4";
  }
}

@media (min-width: 1140px) {
  .main-block .cards {
    grid-template-columns: repeat(auto-fit, minmax(320px, 360px));
    gap: 2rem;
    grid-template-areas: "card-1 card-2 card-3" "card-4 card-4 card-4";
  }
}

.main-block .cards-item__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #FFFFFF;
}

.main-block .cards-item__subtitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #F16D7F;
}

.items-block {
  margin-top: 64px;
}

@media (min-width: 768px) {
  .items-block {
    margin-top: 100px;
  }
}

@media (min-width: 1140px) {
  .items-block {
    margin-top: 100px;
  }
}

.items-block__main-title {
  text-align: center;
}

.items-block__main-title h2 {
  font-weight: 400;
  font-size: 30px;
  line-height: 36px;
  color: #222222;
}

.items-block__main-title span {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #9F9F9F;
}

.items-block .items-block__cards {
  margin: 48px 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 1rem;
}

@media (min-width: 768px) {
  .items-block .items-block__cards {
    grid-template-columns: repeat(2, 360px);
    gap: 1rem;
  }
}

@media (min-width: 1140px) {
  .items-block .items-block__cards {
    grid-template-columns: repeat(3, 360px);
    gap: 2rem;
  }
}

.items-block .items-block__cards .item-card {
  display: flex;
  flex-direction: column;
  max-width: 100%;
}

@media (min-width: 768px) {
  .items-block .items-block__cards .item-card {
    max-width: 360px;
  }
}

@media (min-width: 1140px) {
  .items-block .items-block__cards .item-card {
    max-width: 360px;
  }
}

.items-block .items-block__cards .item-card:hover .img-block:after {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(58, 56, 56, 0.86);
}

.items-block .items-block__cards .item-card:hover .img-block__cart {
  display: flex;
}

.items-block .items-block__cards .item-card__body {
  background: #F8F8F8;
  padding: 24px 28px 20px 16px;
}

.items-block .items-block__cards .img-block {
  position: relative;
}

.items-block .items-block__cards .img-block img {
  object-fit: cover;
  width: 100%;
}

.items-block .items-block__cards .img-block__cart {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  display: none;
}

.items-block .items-block__cards .img-block__cart-title {
  cursor: pointer;
  position: relative;
  text-decoration: none;
  color: white;
  border: 1px solid white;
  line-height: 44px;
  font-weight: 400;
  font-size: 14px;
  padding-left: 50px;
  padding-right: 1rem;
}

.items-block .items-block__cards .img-block__cart-title:before {
  position: absolute;
  content: '';
  top: 50%;
  left: 13px;
  transform: translate(0, -50%);
  width: 26px;
  height: 24px;
  background-image: url("../img/item-card/cart.svg");
}

.items-block .items-block__cards .card__title {
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #000000;
  margin-bottom: 12px;
}

.items-block .items-block__cards .card__subtitle {
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  color: #5D5D5D;
  margin-bottom: 18px;
}

.items-block .items-block__cards .card__price {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #F16D7F;
}

.items-block .btn-all-product {
  display: inline-block;
  padding: 0 39px;
  line-height: 48px;
  border: 1px solid #FF6A6A;
  font-weight: 400;
  font-size: 16px;
  color: #F26376;
  text-decoration: none;
  text-align: center;
}

.items-block .btn-all-product:hover {
  background: #F16D7F;
  border: 1px solid #FF6A6A;
  color: #fff;
}

.items-block__btn {
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
}
